define([
    'jquery',
    'core/modal_factory',
    'core/str',
    'core/notification'
], function($, ModalFactory, Str, Notification) {
    return {
        init: function() {
            $(document).on('click', '.your-delete-button-class', function(e) {
                e.preventDefault();
                const deleteUrl = $(this).attr('href');

                // First modal
                ModalFactory.create({
                    title: 'Delete confirmation',
                    body: 'Are you sure you want to delete this instance?',
                    type: ModalFactory.types.SAVE_CANCEL
                }).then(function(modal) {
                    modal.show();

                    modal.getRoot().on('click', '.btn-primary', function() {
                        modal.hide();

                        // Now your second modal
                        ModalFactory.create({
                            title: 'Double check',
                            body: 'This action is irreversible. Do you really want to continue?',
                            type: ModalFactory.types.SAVE_CANCEL
                        }).then(function(secondModal) {
                            secondModal.show();

                            secondModal.getRoot().on('click', '.btn-primary', function() {
                                window.location.href = deleteUrl;
                            });
                        }).fail(Notification.exception);
                    });
                }).fail(Notification.exception);
            });
        }
    };
});
